const enum subNodeTypes {
	pureText = 0,
	picture = 1,
	link = 2,
	richText = 3,
	attachment = 4,
}


abstract class SubNode {

	type: number;
	content: string;
	post: () => void;


	static creator(type: number, content?: string): SubNode {
		switch (type) {
			case subNodeTypes.pureText:
				return new PureTextNode(content);
			case subNodeTypes.picture:
				return new PictureNode(content);
		}
	}

}

class PureTextNode extends SubNode {
	constructor(content?: string) {
		super();
		this.type = subNodeTypes.pureText;
		if (content)
			this.content = content;
	}

	set content(content: string) {

	}

	get content(): string {
		return '';
	}

	post(){

	}
}

class PictureNode extends SubNode {
	constructor(content?: string) {
		super();
		this.type = subNodeTypes.pureText;
		if (content)
			this.content = content;
	}

	set content(content: string) {

	}

	get content(): string {
		return '';
	}

	post(){

	}
}

export {SubNode, PureTextNode, PictureNode, subNodeTypes};